import React, {useState} from 'react';
import {Panel, PanelHeader, Group, View, Avatar, Card, CardGrid, Separator, Progress,
        Div, Header, InfoRow, Snackbar} from "@vkontakte/vkui";
import Icon24Like from '@vkontakte/icons/dist/24/like';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon24Education from '@vkontakte/icons/dist/24/education';
let Settings = ({user, id, fetchedUser, go}) => {
    let [snackbar, setSnackbar] = useState(null);
    console.log('fetcheduser = ', fetchedUser)
    console.log('user = ', user)
    let openLife = () => {
        console.log('openLife()');
        if (snackbar) return;
        setSnackbar(
            <Snackbar
            layout="vertical"
            before={<Icon24Like style={{color: '#ED574A'}}/>}
            onActionClick={() => console.log('click')}
            onClose={() => setSnackbar(null)}
          >
            Жизни являются показателем здоровья пользователя
          </Snackbar>
        )
    }
    let openMana = () => {
        if (snackbar) return;
        setSnackbar(
            <Snackbar
                layout="vertical"
                before={<Icon24Flash style={{color: '#3F8AE0'}}/>}
                onActionClick={() => console.log('click')}
                onClose={() => setSnackbar(null)}
            >
                Мана как ресурс постоянно израсходуется, будьте осторожны и вовремя выполняйте задания!
            </Snackbar>
        )
    }
    let openKarma = () => {
        if (snackbar) return;
        setSnackbar(
            <Snackbar
                layout="vertical"
                before={<Icon24Favorite style={{color: '#f0945b'}}/>}
                onActionClick={() => console.log('click')}
                onClose={() => setSnackbar(null)}
            >
                Выполняйте задания и увеличивайте кол-во кармы, чтобы выше подниматься в таблице рейтинга!
            </Snackbar>
        )
    }
    return (
        <View id={id} activePanel={id}>
            <Panel id={id} style={{position: 'relative', height: 'calc(100vh - 70px)'}}>
                <PanelHeader>Мой профиль</PanelHeader>
                {/* <Group> */}
                    <Div style={{ display: 'flex', justifyContent: 'center'}}>
                        <Avatar src={`${ user ? user.photo : 'https://sun9-3.userapi.com/c639822/v639822055/55abd/Y4nIaWtNYVI.jpg'}`} size={120}/>
                    </Div>
                {/* </Group> */}
                    <Div style={{ display: 'flex', justifyContent: 'center', fontWeight: 'bold', fontSize: '23px'}}>
                        {fetchedUser ? `${user.first_name} ${user.last_name}`: "Name Surname"}
                    </Div>
                    {/* <Separator/> */}
                    <Div style={{width: 'calc(100% - 32px)', textAlign: 'center', fontWeight: 'bold', }}>
                        {user.level} УРОВЕНЬ
                    </Div>
                    <Div>
                    <InfoRow header={`${user.currentlvl}/${user.maxlvlpoints}`}>
                        <div style={{background: '#EBEDF0', width: '100%', height: 4, borderRadius: 10}}>
                            <div style={{width: `${Math.floor(user.currentlvl/user.maxlvlpoints * 100)}%`, background: '#77ba7c', height: '100%', borderRadius: 10}}>

                            </div>
                        </div>
                    </InfoRow>
                    </Div>
                    <Separator/>
                    <Group separator="hide" header={<Header>Мои характеристики</Header>}>
                        <CardGrid>
                            <Card size='1' mode='shadow' style={{width: 'calc(100% - 10px)', height: '120px', padding: '5px', borderRadius: 5}} >
                            <Header mode='secondary' onClick={openLife} aside={<div style={{display: 'flex', fontWeight: 'bold', alignItems: 'center'}}>
                                    {/* <div style={{marginRight: 20}}>
                                        <Progress value={user.karma} style={{zIndex: 10, width: 130}}/>
                                    </div> */}
                                    <InfoRow style={{marginRight: 20}}>
                                        <div style={{background: '#EBEDF0', width: 130, height: 4, borderRadius: 10, zIndex: 10}}>
                                            <div style={{width: `${Math.floor(user.karma/100 * 130)}px`, background: '#ed574a', height: '100%', borderRadius: 10}}>

                                            </div>
                                        </div>
                                    </InfoRow>
                                    <div style={{color: '#99A2AD', width: 70, display: 'flex', justifyContent: 'flex-end'}}>
                                        {user.karma}/100
                                    </div>      
                                    
                                </div>}>
                                    <span>Жизни</span>
                                    
                                </Header>
                                <Separator />
                                <Header mode='secondary' onClick={openMana} aside={<div style={{display: 'flex', fontWeight: 'bold', alignItems: 'center'}} >
                                    <div style={{marginRight: 20}}>
                                        <Progress value={user.mana} style={{zIndex: 10, width: 130}}/>
                                    </div>
                                    <div style={{color: '#99A2AD', width: 70, display: 'flex', justifyContent: 'flex-end'}}>
                                        {user.mana}/100
                                    </div>      
                                </div>}>
                                
                                    <span>Мана</span>
                                </Header>
                                <Separator />
                                <Header mode='secondary' onClick={openKarma} aside={<div style={{display: 'flex', fontWeight: 'bold', alignItems: 'center'}} >
                                    
                                    <div style={{color: '#99A2AD'}}>
                                        {user.karma}
                                    </div>      
                                </div>}>
                                    <span onClick={openKarma}>Карма</span>
                                </Header>
                            </Card>
                        </CardGrid>
                    </Group>
                    <Div style={{position: 'absolute', top: 60, right: 0, color: '#2975CC'}} onClick={go} data-to="settings">
                        <Icon28SettingsOutline/>
                    </Div>
                    {snackbar}
            </Panel>
        </View>
    )
}

export default Settings;